#country {
    text-align: center;
    outline: none;
}

#select {
 border-style: none;
 border: none;
 outline: none;
 font-size: 2em;
 background-color: transparent;
 color: rgb(79, 235, 149);
 color: white;
 border: 2px solid rgb(79, 235, 149);
 padding: 10px;
 border-radius: 20px;
}

#select option {
    border-style: none;
    border: none;
    outline: none;
    color: rgb(79, 235, 149);
    color: white;
    background-color: black;
}

#select option:hover {
    box-shadow: 0 0 10px 100px #00ff2a inset;
}

#info-container {
    width: fit-content;
    margin: 20px auto;
}

#country h1 {
    color: rgb(79, 235, 149);
    margin-top: 10px;
    text-align: left;
}
#country h1 span{
    color: rgb(235, 106, 106);
    color: white;
}

.show {
    display: block;
}

.hide {
    display: none;
}

.show-select {
    display: inline-block;
    
}

.hide-select {
    display: none;
}

#country table {
    font-size: 2em;
}

#country table th {
    color: var(--green);
    padding: 10px 50px;
}

#country table tr td:first-child {
    color: var(--green);
}

@media (max-width: 850px) {
    #country select {
        font-size: 1.5em;
    }
}

@media (max-width: 750px) {
    #country table {
        font-size: 1.5em;
    }
}

@media (max-width: 650px) {
    #country table {
        font-size: 1.2em;
    }

    #country table th {
        padding: 10px 30px;
    }

    #country select {
        font-size: 1em;
    }
}

@media (max-width: 420px) {
    #country table {
        font-size: 1em;
    }

    #country table th {
        padding: 10px 15px;
    }

    #country select {
        font-size: 0.8em;
    }
}