header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}


.bars {
    font-size: 2.5em;
    color: var(--green);
}

.times {
    font-size: 2.5em;
    display:inline-block;
    margin-top: 17px;
    margin-left: 85%;
    position: relative;
    right: 0;
}


.open {
    position: absolute;
    top: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    height: 120vh;
    width: 30%;
    
    animation: open 500ms ease-out forwards;
}



.closed {
    position: absolute;
    top: 0;
    right: -100vw;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    background-clip: padding-box;
    border: 3px solid transparent;
    height: 100%;
    width: 30%;
    animation: close 500ms ease-in forwards;
}

.navlink {
    color: var(--green);
    text-decoration: none;
}

header ul {
    list-style-type: none;
}

header ul li {
    text-align: center;
    margin-top: 20px;
    font-size: 2em;
}

@keyframes open {
    from {
        top: 0;
        right: -100%;
    }

    to {
        top: 0;
        right: 0;
    }
}

