#home {
    overflow: hidden;
}

#news-container {
    height: 40vw;
    overflow-y: scroll;
}

.articles{
    width: 60vw;
    margin: 20px auto;

}

.articles a {
    color: var(--green);
}

.articles h2{
    color: var(--green);
}

.articles span {
    color: var(--green);
}

@media (max-width: 1100px){
    #news-container {
        height: 45vw;
    }
}

@media (max-width: 1000px){
    #news-container {
        height: 48vw;
    }
}


@media (max-width: 900px){
    #news-container {
        height: 50vw;
    }
}

@media (max-width: 800px){
    #news-container {
        height: 55vw;
    }
}

@media (max-width: 700px){
    #news-container {
        height: 65vw;
    }
}

@media (max-width: 600px){
    #news-container {
        height: 70vw;
    }
}

@media (max-width: 500px){
    #news-container {
        height: 75vw;
        font-size: 0.7em;
    }
    
}

@media (max-width: 400px){
    #news-container {
        height: 83vw;
    }
}

@media (max-width: 300px){
    #news-container {
        height: 88vw;
    }
}
